.ASMEditor {
  width: 100%;
  border: 1px solid #ebebeb;
}
.ASMEditor .ace_gutter-cell.ace_breakpoint {
  position: relative;
}
.ASMEditor .ace_gutter-cell.ace_breakpoint:before {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background-color: #ff2b5d;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

.ASMEditorPCLine {
  background: rgba(7, 216, 44, 0.17) !important;
}